<template>
  <div class="container">
    <div class="container-content">
      <div class="summary">
        <div v-for="item in infoList" :key="item.prop" class="summary-item">
          <img :src="item.imgSrc" class="summary-item-icon" />
          <div class="summary-item-content">
            <div class="summary-item-title">{{ item.label }}</div>
            <div class="summary-item-used">
              {{ itemInfoFormatter(item) }}
            </div>
          </div>
        </div>
      </div>
      <div class="container-bottom">
        <div class="container-form">
          <el-form ref="qryParamRef" :model="qryParam" size="small">
            <el-row :gutter="20">
              <el-col :span="5">
                <el-form-item :label="$t('traffic.memberNumber')" prop="memberNumber">
                  <el-input
                    v-model="qryParam.memberNumber"
                    :placeholder="inputPh('traffic.memberNumber')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="19">
                <el-form-item>
                  <div class="search-button-group">
                    <el-button
                      type="primary"
                      icon="el-icon-search"
                      @click="loadData(false)"
                      >{{ $t('common.query') }}</el-button
                    >
                    <el-button type="info" icon="el-icon-refresh" @click="reset">{{
                      $t('common.reset')
                    }}</el-button>
                  </div>
                </el-form-item>
              </el-col>
            </el-row>
            <div class="tool-button-group">
              <BatchExport
                query-id="xxxx"
                :params="simListExportParam"
                :disabled="tableData.length === 0"
              ></BatchExport>
            </div>
          </el-form>
          <el-table ref="table" v-loading="loading" :data="tableData" :border="true">
            <el-table-column
              prop="accNum"
              :label="$t('traffic.memberNumber')"
              show-overflow-tooltip
            ></el-table-column>
            <!-- <el-table-column
              prop="statusCdDesc"
              label="流量限额（MB）"
              show-overflow-tooltip
            ></el-table-column> -->
            <el-table-column
              prop="statusCd"
              :formatter="rowFormatter"
              :label="$t('traffic.statusCdName')"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column
              prop="effDate"
              :label="$t('traffic.effDate')"
              show-overflow-tooltip
            ></el-table-column>
          </el-table>
          <el-pagination
            v-show="tableData.length > 0"
            background
            :current-page="page.pageIndex"
            align="right"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="page.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          >
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { statusOptions } from '@/utils/constant'
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  data() {
    return {
      grpNumber: this.$route.query.grpNumber,
      tableData: [],
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      detailInfo: {},
      qryParam: {
        memberNumber: '',
      },
    }
  },
  computed: {
    statusOptions() {
      return statusOptions.map((item) => {
        return {
          ...item,
          label: this.$t('simMag.' + item.key),
        }
      })
    },
    simListExportParam() {
      return { ...this.qryParam, grpNumber: this.grpNumber }
    },
    infoList() {
      return [
        {
          label: this.$t('traffic.groupId'),
          formatter: () => this.grpNumber,
          imgSrc: require('@/assets/image/sim/total_use.png'),
        },
        {
          label: this.$t('traffic.memberMag'),
          formatter: () => this.page.total,
          imgSrc: require('@/assets/image/sim/offer_use.png'),
        },
        {
          label: this.$t('traffic.usageAvaMonth'),
          prop: 'supplierDesc',
          imgSrc: require('@/assets/image/sim/pool_use.png'),
        },
        {
          label: this.$t('traffic.usageUsedMonth'),
          prop: 'ispDesc',
          imgSrc: require('@/assets/image/sim/pool_use.png'),
        },
        {
          label: this.$t('traffic.usageRemainMonth'),
          prop: 'custName',
          imgSrc: require('@/assets/image/sim/pool_use.png'),
        },
      ]
    },
  },
  mounted() {
    this.loadData(false)
  },
  methods: {
    rowFormatter(row, col, val) {
      const item = this.statusOptions.find((item) => item.value === val)
      if (item) {
        return this.$t('simMag.' + item.key)
      }
      return val
    },
    async loadData(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        grpNumber: this.grpNumber,
        ...this.qryParam,
        pageInfo: {
          ...this.page,
        },
      }
      this.loading = true
      const res = await fetch('/flowPool/qryBackFlowPoolMemberList', param, {
        method: 'post',
      }).finally(() => {
        this.loading = false
      })
      this.tableData = res.result || []
      this.page.total = res.rowCount
    },
    itemInfoFormatter(obj) {
      const des = obj.formatter ? obj.formatter() : this.detailInfo[obj.prop] ?? '-'
      return des
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.loadData(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.loadData(true)
    },
    exportClick() {},
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.loadData(false)
    },
  },
}
</script>
